body {
  margin: 0;
  padding: 0;
  font-family: 'Merriweather', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.Section {
  display: flex;
}

.direct {
  flex-direction: column;
}

.reverse {
  flex-direction: column-reverse;
}

.Text {
  display: flex;
  align-items: center;
  justify-content: center;
}

.text-content {
  display: block;
}

@media screen and (orientation:landscape) {
  .Section {
    min-height: 100vh;
  }

  .flex {
    width: 50vw;
  }

  .direct {
    flex-direction: row;
  }
  
  .reverse {
    flex-direction: row-reverse;
  }
}

.img {
  display: block;
  width: 100%;
  height: 100%;
  max-height: 100vh;
  object-fit: cover;
}

.title {
  margin-left: 40px;
  margin-right: 40px;
}

.paragraph {
  margin-left: 40px;
  margin-right: 40px;
  line-height: 1.6;
}

.Social {
  background-color: #282c34;
  position: fixed;
  top: 0px;
  right: 0px;
  padding: 5px;
  display: flex;
  flex-direction: column;
}

.logo {
  width: 32px;
  margin: 12px 15px;
}

.Link {
  color: orangered;
  text-decoration: none;
}
